:host, :root {
    --wallet-primary-color: #282829;
    --wallet-background-color: #fff;
    --wallet-medium-gray: #c6ccd0;
    --wallet-border-gray: #e9eaeb;
    --wallet-error-color: #f01a1f;
    --wallet-error-background: #ffebee;
    --md-filled-button-container-color: #282829; 
  }

.shared-wallet {
    display: flex;
    flex-direction: column;
    align-items: center;
  /*  min-height: 100vh; */
    font-family: 'Inter', sans-serif;
    container-name: sharedWallet;
    container-type: inline-size;
    background-color: var(--wallet-background-color);
    padding-top: 32px;
}


.payment-selector {
    width: 100%;
    max-width: 400px;
    border: 0 !important;

    .button + .button{
        margin-left: 0;
        margin-top: 16px
    }
    
    /* Picklist button styling */
    .picklist-button {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 12px 16px;
        background-color: #fff;
        border: 1px solid var(--wallet-border-gray);
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.2s ease;
        
        &:hover {
            border-color: var(--wallet-primary-color);
        }
        
        &::after {
            content: '';
            display: block;
            width: 12px;
            height: 12px;
            background-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTIiIGhlaWdodD0iMTIiIHZpZXdCb3g9IjAgMCAxMiAxMiIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTEwLjI5MyA0LjI5Mkw2IDguNTg2TDEuNzA3IDQuMjkyTDEgNUw2IDEwTDExIDVMMTAuMjkzIDQuMjkyWiIgZmlsbD0iIzI4MjgyOSIvPgo8L3N2Zz4K);
            background-repeat: no-repeat;
            background-position: center;
        }
    }

    .payment-list {
        list-style-type: none;
        padding: 0;
        margin-bottom: 24px;
        margin-top: 0;

        .payment-list-item {
            position: relative;
            border-bottom: solid #E9EAEB 1px;
            cursor: pointer;

            label {
                width: 100%;
                padding: 16px 0;
            }

            input {
                position: absolute;
            }
        }

        .payment-name {
            margin: 0;
        }
    
        .payment-option {
            width: 100%;
            display: flex;
            justify-content: space-between;
            padding-left: 30px;
        }
    }
    

    .selected-payment {
        display: flex;
        align-items: center;
        gap: 8px;
        text-align: right;
        max-width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        flex-wrap: nowrap;
    }
    
    .selected-payment > *:first-child {
        flex: 1;
        min-width: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    
    .selected-payment .icon-payment {
        flex-shrink: 0;
        width: 24px;
        height: 16px;
    }

    .accordion {
        display: none;
    }

    .accordion-body {
        padding-top: 0;
    }

    .accordion-button:not(.collapsed) {
        box-shadow: none;
        color: var(--wallet-primary-color);
    }
}

.add-payment-container {
    width: 100%;
}

/* Collapsible Container */
.collapsible-wrapper {
    overflow: hidden; /* Important for collapsing */

    &.accordion-item {
        border-bottom: solid 1px var(--wallet-border-gray);
    }

    &.accordion-item:first-of-type {
        border-top: solid 1px var(--wallet-border-gray);
    }

    .collapsible-header {
        display: flex;
        justify-content: space-between;
        position: relative;
        width: 100%;
        cursor: pointer;
        padding-right: 14px;
        
        /* Add dropdown arrow with base64 encoding */
        &::after {
            content: '';
            height: 10px;
            width: 16px;
            display: block;
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate(0, -50%);
            background-image: url(data:image/png;base64,);
            background-repeat: no-repeat;
            background-size: contain;
            transition: transform .3s ease;
        }
        
        /* Rotate arrow when expanded */
        .collapsed &::after {
            transform: rotate(180deg);
        }
        padding: 18px 32px 18px 0;
    
        h3, p {
            margin: 0;
        }

        h3 {
            white-space: nowrap;
        }
    }
      
      .collapsible-content {
          transition: height 0.3s ease;
          overflow: hidden;

          >:last-child {
            margin-bottom: 24px;
          }
      }

      &.collapsed {
        .collapsible-header {
            &:after {
                transform: rotate(180deg) translate(0, 50%);;
            }
        }
        
        .collapsible-content {
            height: 0;
        }
      }
      
}

.footer {
    display: flex;
    justify-content: flex-end;
}



* {
    box-sizing: border-box;
}

/* Utilities */
.w-100 {
    width: 100%;
}

/* Form Elements */
form {
    max-width: 100%;
    box-shadow: none;

    div {
        margin-bottom: 15px;
    }

    h4 {
        margin: 0;
    }
}

label {
    display: block;
    font-size: 12px;
    font-weight: 700;
    line-height: 24px;
}

input[type="text"], select {
    margin: 0;
    width: 100%;
    padding: 10px;
    border: 1px solid var(--wallet-border-gray);
    border-radius: 12px;
    font-size: 16px;
    font-weight: 400;
    background-color: #fff;
    transition: border-color 0.3s ease;
}

input:focus {
    border-color: var(--wallet-primary-color); /* Highlight border on focus */
    outline: none;
    box-shadow: 0 0 5px rgba(232, 241, 251, 0.5); /* Add a subtle glow effect */
}

input::placeholder {
    font-style: normal !important;
}

select {
    appearance: none;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-position-y: 50%;
    background-position-x: calc(100% - 16px);
    background-size: 12px 9px;

    &:focus-visible {
        outline: 1px solid var(--wallet-primary-color);
    }

    &:hover {
        cursor: pointer;
    }
}

.custom-radio {
    cursor: pointer;
    position: relative;
    padding-left: 28px;

    input {
        display: none;
        position: relative;
    }
}

.custom-radio::before {
    content: "";
    position: absolute;
    display: block;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    border: solid 1px var(--wallet-medium-gray);
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
    background-color: transparent;
}

.custom-radio:has(input:checked)::before {
    background-color: var(--wallet-primary-color);
    border-color: var(--wallet-primary-color);

}

.custom-radio::after {
    content: "";
    position: absolute;
    display: block;
    height: 6px;
    width: 6px;
    border-radius: 50%;
    background-color: var(--wallet-background-color);
    left: 7px;
    top: 50%;
    transform: translate(0, -50%);
}

.error {
    color: var(--wallet-error-color);
    font-size: 12px;
    margin-top: 5px;
    font-weight: 700;
    display: flex;
    align-items: center;
}

.error:before {
    content: "";
    background-image: url('../../assets/icons/danger.png');
    background-repeat: no-repeat;
    background-size: contain;
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-right: 4px;
    margin-left: 2px;
}

form input.error {
    border-color: var(--wallet-error-color);
    box-shadow: 0 0 1px 0 var(--wallet-error-color);
}


/* End Form Elements */

.api-errors {
    color: var(--wallet-primary-color);
    background: var(--wallet-error-background);
    padding: 12px;
    border-radius: 12px;
    position: relative;
    display: flex;
    gap: 8px;
    align-items: center;

    .error-content {
        margin: 0;
    }

    &:before {
        content: "";
        background-image: url('../../assets/icons/warning.png');
        background-repeat: no-repeat;
        background-size: contain;
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 4px;
        margin-left: 2px;
    }

    h5 {
        margin: 0;
        font-weight: 700;
        font-size: 12px;
    }

    ul {
        margin: 0;
        padding: 0 12px;
        font-size: 12px;
    }
}

/* Typography */
.small-hero {
    font-family: 'Mackinac';
    font-size: 24px;
    font-weight: 800;
}

.text-medium {
    font-family: 'Inter', sans-serif;
