/* Buttons */

.button {
    border-radius: 12px;
    box-shadow: none;
    box-sizing: border-box;
    font-size: 14px;
    text-align: center;
    padding: 12px 16px;
    cursor: pointer;
    --_label-text-weight: 400;
    --_label-text-font: 'Inter', sans-serif; 

    &.button-bold {
        font-weight: 700;
    }

    &.button-thick-border {
        border-width: 2px;
    }

    & + .button {
        margin-left: 12px;
    }
}

.button-primary {
    background-color: var(--wallet-primary-color);
    color: #fff;
    border: 0;


}

.button-secondary {
    background-color: var(--wallet-background-color);
    border: 1px solid var(--wallet-border-gray);
    color: var(--wallet-primary-color);
    --md-filled-button-container-color: #fff;
    --_pressed-state-layer-color: #000;
    --_label-text-color: var(--wallet-primary-color) !important;
    --_hover-label-text-color: var(--wallet-primary-color);
    transition: all 0.3s ease;

    &:hover {
        border-color: var(--wallet-medium-gray);
        --_hover-container-elevation: 0;
    }

    &:active {
        --_pressed-label-text-color: var(--wallet-primary-color);
    }

    &:focus {
        --_focus-label-text-color: var(--wallet-primary-color);
    }
}


/**
 * ==============================================
 * Dot Flashing
 * ==============================================
 */
 .dot-flashing {
    position: relative;
    width: 8px;
    height: 8px;
    border-radius: 5px;
    background-color: var(--wallet-background-color);
    color: var(--wallet-background-color);
    animation: dot-flashing 1s infinite linear alternate;
    animation-delay: 0.5s;
    display: inline-block;
    margin-left: 24px;
    margin-right: 12px;
  }
  .dot-flashing::before, .dot-flashing::after {
    content: "";
    display: inline-block;
    position: absolute;
    top: 0;
  }
  .dot-flashing::before {
    left: -12px;
    width: 8px;
    height: 8px;
    border-radius: 5px;
    background-color: var(--wallet-background-color);
    color: var(--wallet-background-color);
    animation: dot-flashing 1s infinite alternate;
    animation-delay: 0s;
  }
  .dot-flashing::after {
    left: 12px;
    width: 8px;
    height: 8px;
    border-radius: 5px;
    background-color: var(--wallet-background-color);
    color: var(--wallet-background-color);
    animation: dot-flashing 1s infinite alternate;
    animation-delay: 1s;
  }
  
  @keyframes dot-flashing {
    0% {
      background-color: var(--wallet-background-color);
    }
    50%, 100% {
      background-color: rgba(255, 255, 255, 0.1);
    }
  }


/* Grid */
.wallet-row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -8px;
    margin-right: -8px;

    [class^="wallet-col-"] {
        flex-basis: 100%;
        padding-left: 8px;
        padding-right: 8px;
    }
}

/* Small */
@container sharedWallet (min-width: 576px) {
    .wallet-row {
        .wallet-col-sm-6 {
            flex-basis: 50%;
        }
    }
}

/* Medium */
@container sharedWallet (min-width: 768px) {
    .wallet-row {
        .wallet-col-md-6 {
            flex-basis: 50%;
        }

        .wallet-col-md-4 {
            flex-basis: 33.33%;
        }
    }
}

/* Large */
@container sharedWallet (min-width: 992px) {
    .wallet-row {
        .wallet-col-lg-6 {
            flex-basis: 50%;
        }

        .wallet-col-lg-3 {
            flex-basis: 25%;
        }

        .wallet-col-lg-4 {
            flex-basis: 33.3333%;
        }
    }
}

/* Extra Large */
@container sharedWallet (min-width: 1200px) {
    .wallet-row {
        .wallet-col-xl-6 {
            flex-basis: 50%;
        }
    }
}

.input-wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 0px;
}

.field-stack {
    display: flex;
    gap: 10px;
    align-items: baseline;
    margin-bottom: 0px;
}

.field-stack.dual-field .input-wrapper:first-child {
  width: 25%;
}
 
.field-stack.dual-field .input-wrapper:last-child {
  width: 75%;
}
 
.field-stack.single-field .input-wrapper {
  width: 100%;
}


/* Flatpickr */
.flatpickr-day.selected, 
.flatpickr-day.startRange, 
.flatpickr-day.endRange, 
.flatpickr-day.selected.inRange, 
.flatpickr-day.startRange.inRange, 
.flatpickr-day.endRange.inRange, 
.flatpickr-day.selected:focus, 
.flatpickr-day.startRange:focus, 
.flatpickr-day.endRange:focus, 
.flatpickr-day.selected:hover, 
.flatpickr-day.startRange:hover, 
.flatpickr-day.endRange:hover, 
.flatpickr-day.selected.prevMonthDay, 
.flatpickr-day.startRange.prevMonthDay, 
.flatpickr-day.endRange.prevMonthDay, 
.flatpickr-day.selected.nextMonthDay, 
.flatpickr-day.startRange.nextMonthDay, 
.flatpickr-day.endRange.nextMonthDay {
    background-color: var(--wallet-primary-color);
    border-color: var(--wallet-primary-color);
}


